import {
    MSG_ALLOW_TEMPORARILY,
    MSG_COLOR_SCHEME_CHANGED,
    MSG_INFO_BLOCKED_CONTENT_DISPLAYED,
    MSG_INFO_BREACH_NOTIFICATION_DISPLAYED,
    MSG_RECORDS_GET,
    RELEASE_NOTIFICATION_DEAFULT_TEXT
} from "../app/scripts/app-consts";
